	function expandSearchOnInputChange() {
		$('#inpt_search').on('input', function () {
			if ($(this).val() !== '') {
				$(this).parent().addClass('active');
			}
			else {
				$(this).parent().removeClass('active');
			}
		});

		$(window).click(function() {
			if ($('#inpt_search').val() === '') {
				$('#inpt_search').parent().removeClass('active');
			}
		});

		$('#inpt_search').on('click', function (event) {
			event.stopPropagation();
			$(this).parent().addClass('active');
		});
	}

	function technologySubMenu(){
		const menuElems = document.querySelectorAll('.menu__item');
		const technologyNav = $('.m-nav__technologies');
		const ww = $(window).width();

		if ( ww >= 992 ) {
			menuElems.forEach((elem) => elem.addEventListener('mouseover', slideMenuShow ));
			menuElems.forEach((elem) => elem.addEventListener('mouseleave', slideMenuHide ));

			function slideMenuShow(e) {
				const item = this.children[0].textContent.replace(/\s/g, '').toLowerCase();
				e.stopPropagation();

				if (item === 'technologies') {
					$(this).addClass('technologies__link--active hover');
					technologyNav.stop().slideDown().addClass('m-nav-technologies--active');
				} else {
					$('.m-nav__menu .technologies__link--active').removeClass('hover');
				}
			}

			function slideMenuHide(e) {
				const item = this.children[0].textContent.replace(/\s/g, '').toLowerCase();
				e.stopPropagation();

				if (e.target !== (item === 'technologies')) {
					technologyNav.stop().slideUp().removeClass('m-nav-technologies--active');
				} else {
					technologyNav.stop().slideDown().addClass('m-nav-technologies--active');
				}
				if ((!$('.m-nav__technologies').hasClass('.m-nav__technologies--active'))) {
					$('.m-nav__menu .technologies__link--active').removeClass('hover');
				}

			}

			technologyNav.on('mouseover', function (e) {
				e.stopPropagation();
				technologyNav.stop().slideDown().addClass('m-nav-technologies--active');
				$('.m-nav__menu [data-dropdown]').addClass('hover');
			});

			technologyNav.on('mouseleave', function (e) {
				e.stopPropagation();
				technologyNav.stop().slideUp().removeClass('m-nav-technologies--active');
				$('.m-nav__menu .technologies__link--active').removeClass('hover');
			});
		}
	}

