	function syntaxHighlight() {
		var languageItems = [];
		var languages = components.languages;
		var count = 0;
		for (var id in languages) {
			if (id == 'meta') {
				continue;
			}
			count++;
			var name = languages[id].title || languages[id];

			languageItems.push({
				tag: 'li',
				attributes: {
					'data-id': id
				},
				contents: [
					name,
					' - ',
					{
						tag: 'code',
						contents: id
					}
				]
			});
		}
		$u.element.create('ul', {
			contents: languageItems,
			inside: '#languages-list'
		});
		$u.element.contents($('#languages-list-count'), count);
	}

	function animationFrames() {
		const windowWidth = $(window).width();
		if ( windowWidth > 1024) {

			const requestAnimFrame = window.requestAnimationFrame ||
			window.webkitRequestAnimationFrame ||
			window.mozRequestAnimationFrame ||
			window.msRequestAnimationFrame ||
			window.oRequestAnimationFrame ||
			// IE Fallback, you can even fallback to onscroll
			function(callback){ window.setTimeout(callback, 1000/60) };

			function detectScroll() {
				const wScroll = window.pageYOffset;

				$('.intro__box--logo').css({
					'transform' : 'translate3d(0, -' +  (wScroll / 6) +'%, 0)',
					'opacity' : 1,
					'transition': 'opacity .5s ease-in',
				});

				if ( wScroll >= 1 ){
					$('.intro__box--logo').css({ 'opacity': 0 });

				} else {
					$('.intro__box--logo').fadeIn('1000');
				}

				if ( $('.dev-website').length > 0 ) {

					if ( wScroll > $('.dev-website').offset().top - $(window).height()) {

						const webOffset = wScroll - ($('.dev-website').offset().top - 200 ) + ($(window).height() / 20) ;
						const webOffsetContainer = $('.dev-website').height();
						const mobileOffset = wScroll - ($('.dev-mobile').offset().top - 200 ) + ($(window).height() / 20 );
						const mobileOffsetCotainer = $('.dev-mobile').height();
						const softwareOffset = wScroll - ($('.dev-software').offset().top - 200 ) + ($(window).height() / 10 );
						const softwareOffsetContainer = $('.dev-software').height();

						if ( webOffset > 0 && wScroll <= $('.dev-website').offset().top + webOffsetContainer) {
							$('.dev-website img').css({ 'transform' : 'translate3d(0, -' + (webOffset / 2) / 9 +'%, 0)' });
						}

						if ( mobileOffsetCotainer > 0 && wScroll <= $('.dev-mobile').offset().top + mobileOffsetCotainer) {
							$('.dev-mobile img').css({ 'transform' : 'translate3d(0, -' + (mobileOffset / 2) / 9 +'% , 0)' });
						}

						if ( softwareOffsetContainer > 0 && wScroll <= $('.dev-software').offset().top + softwareOffsetContainer) {
							$('.dev-software img').css({ 'transform' : 'translate3d(0, -' + (softwareOffset / 2) / 9 +'%, 0)' });
						}
					}
				};

				requestAnimFrame( detectScroll );
			}
			detectScroll();
		}
	}

